.cards {
  width: 880px;
  height: 743px;
  margin-top: 40px;
  margin-left: 200px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  column-gap: 17px;
  row-gap: 21px;
  position: absolute;
}

.cards__card-owner {
  display: none;
}

.cards__card-created {
  display: none;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .cards {
    width: 282px;
    height: 2266px;
    margin-top: 36px;
    margin-left: 19px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(6, 1fr);
    column-gap: 0;
    row-gap: 0;
    position: absolute;
  }
}
