* {
  font-family: "Inter", Arial, sans-serif;
  color: #ffffff;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  background: #000000;
}

.page {
  height: 100%;
  max-width: 1280px;
  margin: 0 auto;
  background: #000000;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .page {
    height: 100%;
    max-width: 320px;
    margin: 0 auto;
    background: #000000;
  }
}
