.profile__name {
  width: 373px;
  height: 48px;
  font-size: 42px;
  line-height: 48px;
  display: flex;
  font-weight: 500px;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 0;
  padding: 0;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .profile__name {
    width: 196px;
    height: 24px;
    margin-left: 29px;
    text-align: center;
    font-size: 22px;
    line-height: 26.63px;
    display: flex;
    margin-bottom: 0;
    padding-bottom: 0;
  }
}
