.popupAvatarUpdate__form {
  display: flex;
  flex-direction: column;
  width: 430px;
  height: 330px;
  margin: 0 auto;
  border: 1px solid #000000;
  border-radius: 10px;
  box-shadow: 0px 0px 25px 0px #00000026;
  background-color: #ffffff;
  position: relative;
  color: #000000;
  transition-timing-function: ease;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .popupAvatarUpdate__form {
    width: 100%;
    max-width: 300px;
  }
}
