.popup__closeButton {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 120px;
  right: 420px;
  border: #000000;
  cursor: pointer;
  z-index: 1000;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .popup__closeButton {
    width: 26px;
    height: 26px;
    position: absolute;
    top: 15px;
    right: 10px;
  }
}
