.profile__addButton {
  width: 150px;
  height: 50px;
  border: 2px solid #ffffff;
  color: #ffffff;
  background: #000000;
  margin-top: 35px;
  cursor: pointer;
}

.profile__addButton-cards {
  width: 22px;
  height: 22px;
  margin-left: 64px;
  margin-top: 14px;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .profile__addButton {
    width: 282px;
    height: 50px;
    border: 2px solid #ffffff;
    color: #ffffff;
    background: #000000;
    margin-top: 0;
    padding-top: 0;
  }

  .profile__addButton-cards {
    width: 16px;
    height: 16px;
    margin-left: 133px;
    margin-top: 17px;
  }
}
