.profile__column {
  display: grid;
  width: 880px;
  height: 120px;
  grid-template-columns: 150px 580px 150px;
  grid-template-rows: repeat(1, 1fr);
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .profile__column {
    display: grid;
    width: 282px;
    height: 284px;
    grid-template-columns: 282px;
    grid-template-rows: 150px 80px 50px;
  }
}
