.popup__form {
  display: flex;
  flex-direction: column;
  width: 430px;
  height: 330px;
  margin: 0 auto;
  border: 1px solid #000000;
  border-radius: 10px;
  box-shadow: 0px 0px 25px 0px #00000026;
  background-color: #ffffff;
  position: relative;
  color: #000000;
  transition-timing-function: ease;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .popup__form {
    width: 282px;
    height: 322px;
    max-width: 100%;
    border: 1px solid #000000;
    border-radius: 10px;
    box-shadow: 0px 0px 25px 0px #00000026;
    background-color: #ffffff;
    color: #000000;
    position: relative;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
