.imageDisplay__img {
  max-width: 75vw;
  max-height: 75vw;
  margin-bottom: 0;
  object-fit: contain;
  border-radius: 10px 10px 0px 0px;
}

.imageDisplay__title {
  width: 100%;
  margin-top: 10px;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.2;
  text-align: center;
  color: #ffffff;
  padding: 10px 0;
}
@media screen and (max-width: 320px) and (max-height: 2814px) {
  .imageDisplay__img {
    width: 100%;
    height: auto;
  }
}
