.popupAvatarUpdate__form-link {
  margin-top: 54px;
  width: 358px;
  height: 27.34px;
  font-size: 14px;
  font-weight: 400px;
  line-height: 16.94px;
  border: 0;
  border-bottom: 1px solid #00000033;
  margin-left: 36px;
  color: #000000;
  align-items: center;
}

.popupAvatarUpdate__form-link:valid {
  border-bottom: 1px solid green;
  color: #000000;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: 0em;
  text-align: left;
  padding-bottom: 2.7px;
}

.form-input_type_error {
  border-bottom: 1px solid red;
  color: #000000;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: 0em;
  text-align: left;
  padding-bottom: 2.7px;
}

.form-input-error {
  font-size: 12px;
  width: 358px;
  margin-top: 5px;
  color: #ff0000;
  margin-left: 40px;
  font-weight: 400px;
  line-height: 14.52px;
  display: flex;
  position: fixed;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .popupAvatarUpdate__form-link {
    margin-top: 30px;
    width: 238px;
    height: 26px;
    font-size: 14px;
    font-weight: 400px;
    line-height: 19.94px;
    border: 0;
    border-bottom: 1px solid #000000;
    margin-left: 22px;
    color: #000000;
  }
}
