.profile__explorer {
  width: 415px;
  height: 22px;
  font-weight: 400;
  margin-top: 18px;
  font-size: 18px;
  line-height: 21.78px;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .profile__explorer {
    width: 282px;
    height: 16px;
    font-weight: 400px;
    padding-top: 0;
    margin-top: 0;
    text-align: center;
    font-size: 14px;
    line-height: 16.94px;
  }
}
