.cards__card {
  width: 282px;
  height: 361px;
  border-radius: 10px;
  background-color: #ffffff;
  position: relative;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .cards__card {
    width: 282px;
    height: 361px;
    position: relative;
  }
}
