.profile__info {
  width: 416px;
  height: 80px;
  color: #ffffff;
  margin-top: 22px;
  display: grid;
  grid-template-columns: 373px 24px;
  grid-template-rows: 39px 38px;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .profile__info {
    width: 282px;
    height: 50px;
    color: #ffffff;
    display: grid;
    grid-template-columns: 225px 57px;
    grid-template-rows: 30px 20px;
    margin-top: 0;
    padding-bottom: 0;
  }
}
