.popup__form-Explorar {
  margin-top: 15px;
  width: 358px;
  height: 27px;
  font-size: 14px;
  font-weight: 400px;
  line-height: 19.94px;
  border: 0;
  border-bottom: 1px solid #00000033;
  margin-left: 36px;
  color: #000000;
}

.popup__form-Explorar:valid {
  border-bottom: 1px solid green;
}

.form-input_type_error {
  border-bottom: 1px solid red;
}

.form-input-error {
  margin-top: 0;
  font-size: 10px;
  color: red;
  margin-left: 40px;
  padding-right: 30px;
  font-weight: 400px;
  line-height: 14.52px;
  position: fixed;
  display: flex;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .popup__form-Explorar {
    margin-top: 30px;
    width: 238px;
    height: 26px;
    font-size: 14px;
    font-weight: 400px;
    line-height: 19.94px;
    border: 0;
    border-bottom: 1px solid #000000;
    margin-left: 22px;
    color: #000000;
  }
}
