@keyframes fade_in_show {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.imageDisplay {
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 9999;
  position: fixed;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  animation: fade_in_show 1s;
}

.imageDisplay_change_display {
  display: flex;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .imageDisplay {
    width: 320px;
    height: 2814px;
    max-width: 100%;
  }

  .imageDisplay_change_display {
    justify-content: left;
  }
}
