.popupWithConfirmation__submit-save {
  width: 358px;
  height: 50px;
  margin-top: 10px;
  margin-left: 36px;
  border-radius: 2px;
  border: 1px;
  font-weight: 400px;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: #ffffff;
  background-color: #000000;
  cursor: pointer;
}
.popupWithConfirmation__submit-save_inactive {
  color: #ffffff;
  background: #00000010;
  pointer-events: none;
  user-select: none;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .popupWithConfirmation__submit-save {
    width: 238px;
    height: 46px;
    margin-top: 46px;
    margin-left: 22px;
    border-radius: 2px;
    border: 1px;
    font-weight: 400px;
    font-size: 14px;
    line-height: 16.94px;
    text-align: center;
    color: #ffffff;
    background-color: #000000;
    cursor: pointer;
  }
}
