.imageDisplay__zoom {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 90%;
  max-height: 90%;
  position: relative;
  overflow: hidden;
  margin-top: 100px;
  border-radius: 10px;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .imageDisplay__zoom {
    width: 240px;
    height: auto;
  }
}
