@keyframes fade_in_show {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.popup {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 9999;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  animation: fade_in_show 0.8s;
}
.popup__content {
  display: flex;
  flex-direction: column;
  max-width: 80%;
  max-height: 80%;
  margin: 0 auto;
  border-radius: 10px;
  position: relative;
}

.popup_change_display {
  visibility: visible;
  display: flex;
  margin-top: auto;
  position: fixed;
}

.popup__image {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.popup__caption {
  margin-top: 10px;
  font-size: 16px;
  text-align: center;
  color: #333;
}

@media screen and (max-width: 320px) and (max-height: 2814px) {
  .popup {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    align-content: center;
    padding-top: 40px;
    overflow-y: auto;
  }

  .popup__content {
    width: 90%;
    max-width: 300px;
  }
}
